	module.exports = {"data":{"site":{"siteMetadata":{"title":"Psyked.co.uk","author":"James Ford"}},"markdownRemark":{"id":"/Users/James/Repositories/portfolio-2018/src/posts/the-results-of-my-annual-code-something-different-this-christmas-idea/index.md absPath of file >>> MarkdownRemark","html":"<p>It's becoming something of a tradition now I guess, but once I've allocated that extra Christmas holiday time appropriately, I find I have that little bit of extra time to devote to trying something new.  This year, I thought I'd try rewriting (again) one of my old university experiments.</p>\n<p>Here's the result:</p>\n<p>[kml_flashembed movie=\"http://uploads.psyked.co.uk/2010/01/AvatarGame.swf\" height=\"280\" width=\"500\" /]</p>\n<p>It's pretty.  I won't even try to explain the idea behind it, but it's basically an attempt at simple life simulation, with some AI governing the actions.  This time around I've built it with the aid of Box2D, so all the movement, impacts and interaction is achieved by punching some figures into the Box2D world, which makes the thing a helluva lot easier.</p>\n<p>The big plus of \"trying something new\"? I now have a basic understanding of trigonometry!  I didn't even have to Google anything!</p>","frontmatter":{"title":"The results of my annual \"code something different this Christmas\" idea.","date":"January 04, 2010","tags":["actionscript","box2d"],"url":null}}},"pathContext":{}}

